import { useContext, useEffect, useState, useRef, createRef } from "react";
import { InitContext } from "../../contexts/initContext";
import { useTranslation } from "react-i18next";
import { v4 as uuidv4 } from 'uuid';
import { FaCheck, FaExclamation } from "react-icons/fa";
import { BsPencil} from "react-icons/bs";

import PriceCard from "../website/PriceCard";
import "../../styles/ServicePackage.scss"
import { DataContext } from "../../contexts/dataContext";
import MsgTemplate from "./MsgTemplate";

function ServicePackage(props) {
    const { 
        TransactionServicepackage,
        TransactionUserServicepackage,
        plans
    } = useContext(InitContext);
    const { 
        thisUser, thisPackage,
        remark, setRemark
    } = useContext(DataContext)
    const { t, i18n } = useTranslation();
    const [selectedPlan, setSelectedPlan]=useState({packageid: ""})
    const [isPriceCardDiv, setIsPriceCardDiv] = useState(false) 
    const [orderValue, setOrderValue] = useState({})
    const [step, setStep] = useState(1)
    const formRef = useRef()
    const [msg, setMsg] = useState(null)
    const [expiration, setExpiration] = useState({
        validDate:"",
        expireDate: ""
    })



    const clickPlanBtn = (e, plan) =>{
        console.log(plan)
        setSelectedPlan(plan)
        setStep(2)
        // setIsPriceCardDiv(false)
        var date=new Date();
        date.setFullYear(date.getFullYear()+1)
        date.setDate(date.getDate()-1)
        setExpiration({
            validDate: new Date().toISOString(),
            expireDate: date.toISOString()
        })
    }

    const clickNextBtn = e => setStep(3)

    const clickPen = e => {
        setSelectedPlan({packageid: ""})
        setStep(e)
    }

    const changeInput = e => {
        // setOrderValue({...orderValue, [e.target.name]: e.target.value})
    }

    const submitOrder = e => {
        e.preventDefault();
        console.log(orderValue)
        console.log(formRef)

        var formData = new FormData(formRef.current)
        var object = {};
        formData.forEach((value, key) => object[key] = value);
        console.log(object)

        console.log({
            action: "Update",
            packageid: selectedPlan.packageid,
            userid: thisUser.userid,
            enabled: 1,
            expiration: expiration.expireDate
            // ...object
        })

        fetch(TransactionUserServicepackage, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                'Authorization': `Bearer ${localStorage.getItem("token")}`
            },
            body: JSON.stringify({
                action: "Update",
                packageid: selectedPlan.packageid,
                userid: thisUser.userid,
                enabled: 1,
                expiration: expiration.expireDate
                // // ...object

                // "action": "Update",        
                // "packageid": selectedPlan.packageid,  
                // "userid": "new",       
                // "enabled": "1"           
       
            })
        })
        .then(res=>res.json())
        .then(data =>{
            console.log(data)
            if(data.result.toLowerCase().indexOf("ok")!==-1) {
                setMsg({...data, title: t("awesome"), text: t("order confirm"), icon: <FaCheck/> })
            } else {
                setMsg({...data, title: data.result, icon: <FaExclamation />})
            }
        })
        .catch(err => console.error(err))
    }

    const clickMsg = e => {
        setMsg(null)
        props.setIsUpgrade(false)
    }

    useEffect(()=>{
        fetch(TransactionServicepackage, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                'Authorization': `Bearer ${localStorage.getItem("token")}`
            },
            body: JSON.stringify({
                "action": "Query",
                "packageid": "",
                "packagetype": "",    
                "enabled": "",
            })
        })
        .then(res=>res.json())
        .then(data=> {
            console.log(data)
        })
        .catch(err => console.error(err))
    }, [TransactionServicepackage])


    return(
        <div className="ServicePackage">
            <section data-step="1">
                <h1 className="step1">
                    <span>1. {t("choose_one_plan")}</span>
                    {step!==1 && <BsPencil onClick={()=>clickPen(1)}/>}
                    </h1>
                {
                    <div className="price-card-div" data-step={step!==1 ? "selected" : ""}>
                    { 
                        plans.map((plan, i)=>
                            // <div key={uuidv4()} className={plan.packageid===thisPackage.packageid ? "thisPackage" : (plan.packageid===selectedPlan.packageid ? "selectedPlan" : "")}>
                            <div key={uuidv4()}>
                                <PriceCard 
                                    t={t} 
                                    plan={plan} 
                                    linkto={"#"} 
                                    linkText={plan.packageid===thisPackage.packageid ? t("your_package") : (plan.packageid===selectedPlan.packageid ? <FaCheck/> : t("upgrade"))} 
                                    i18n={i18n} 
                                    onClick={(e)=>clickPlanBtn(e, plan)} 
                                    />
                            </div>)
                    }
                </div>    
                }
            </section>
            <section>
                <h1 className="step2">2. {t("plan_details")}</h1>
                {
                    selectedPlan!==null && step>1 && <>
                    <div>
                        <h1>{selectedPlan.packageid}</h1>
                        <h1><span>{t("selected_plan")}: </span>{i18n.language==="en" ? selectedPlan.packagename_en : selectedPlan.packagename_cht}</h1>
                        <p><span className="price">${selectedPlan.price}</span>/{t("month")}</p>
                        <p>{t("total_amount")}<span className="price">{selectedPlan.price*12}</span>/{t("year")}</p>
                        <p>{t("valid from")}: {expiration.validDate}</p>
                        <p>{t("expire to")} {expiration.expireDate} </p>
                    </div>
                    {step!==3 && <button onClick={clickNextBtn}>{t("next")}</button>}
                    </>
                }
                </section>   
            <section>
                <h1 className="step3">3. {t("billing address and payment details")}</h1>
                {step>2 && <>
                    <form  onSubmit={submitOrder} action="/MyAccount.js" ref={formRef}>
                    <div className="personal-info-inputs">

                            {/* <label>
                                <p>{t("userid")}</p>
                                <input name="userid" defaultValue={thisUser.userid} className="disabled-input" />
                            </label>
                            <label>
                                <p>{t("orderdate")}</p>
                                <input name="orderdate" type="text"  defaultValue={new Date().toLocaleDateString().replaceAll("/", "-")} onChange={changeInput} className="disabled-input" />
                            </label>
                            <label>
                                <p>{t("amount")}</p>
                                <input name="amount" type="text"  defaultValue={selectedPlan.price*12} onChange={changeInput} className="disabled-input" />
                            </label>                         */}
                        {/* <div className="input-div">
                            <label>
                                <p>{t("payno")}</p>
                                <input name="payno" type="tel"  onChange={changeInput} pattern="^[0-9-+\s()]*$" />
                            </label>
                            <label>
                                <p>{t("invoice")}</p>
                                <input name="invoice" type="text" defaultValue="" onChange={changeInput} /> 
                            </label>
                            <label>
                                <p>{t("taxidnumber")}</p>
                                <input name="taxidnumber" type="tel"  onChange={changeInput} pattern="^[0-9-+\s()]*$" />
                            </label>
                            <label>
                                <p>{t("orderstatus")}</p>
                                <select name="orderstatus">
                                    <option value="0">{t("processing")}</option>
                                    <option value="1">{t("completed")}</option>
                                    <option value="3">{t("cancel")}</option>                                    
                                </select>
                            </label>
                            <label>
                                <p>{t("paidstatus")}</p>
                                <select name="paidstatus">
                                    <option value="0">{t("unpaid")}</option>
                                    <option value="1">{t("paid")}</option>
                                    <option value="3">{t("refund")}</option>                                   
                                </select>
                            </label>
                            <label>
                                <p>{t("deliverystatus")}</p>
                                <select name="deliverystatus">
                                    <option value="0">{t("instock")}</option>
                                    <option value="1">{t("delivered")}</option>
                                    <option value="3">{t("returned")}</option>                                   
                                </select>
                            </label>
                        </div> */}
                                {/* <label>
                                    <p>{t("taxaddress")}</p>
                                    <input name="taxaddress" type="text" defaultValue={thisUser.address} onChange={changeInput} /> 
                                </label>  
                                <label>
                                    <p>{t("shipaddress")}</p>
                                    <input name="shipaddress" type="text" defaultValue={thisUser.address} onChange={changeInput} /> 
                                </label>   */}
                        </div>
                        <div>
                            <h1>{i18n.language==="en" ? selectedPlan.packagename_en : selectedPlan.packagename_cht}</h1>
                            <p>{t("total_amount")}<span className="price">{selectedPlan.price*12}</span>/{t("year")}</p>
                        </div>                    
                        <button>{t("pay")}</button>
                    </form>                   
                </>}
      
                </section>     
                {
                    msg!==null && <MsgTemplate 
                                    onClick={clickMsg}
                                    msg={msg}
                                    />          
                }

        </div>
    )
}

export default ServicePackage;